import axios from "axios";

//
export function host_hosts(queryString) {
  return axios({
    method: "GET",
    url: `https://13.124.69.255:90/api/host/GetHost_List?${queryString}`,
    mode: "no-cors",
  });
}
export function create_host_host(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/host_detail/Save_Host",
    data: body,
    mode: "no-cors",
  });
}
export function update_host_host(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/host_detail/Save_Host",
    data: body,
    mode: "no-cors",
  });
}
export function delete_host_host(body) {
  return axios({
    method: "DELETE",
    url: "https://13.124.69.255:90/api/host/Delete_Host",
    data: { idxs: [`${body.idx}`] },
    mode: "no-cors",
  });
}
